<template>
  <div class="alert">
    <header :class="addClass">{{ which }}</header>
    <p><slot></slot></p>
  </div>
</template>

<script>
export default {
  props: ["info", "which"],

  computed: {
    addClass() {
      if (this.which === "Information") {
        return "info";
      } else if (this.which === "OK") {
        return "ok";
      } else if (this.which === "Error") {
        return "error";
      }

      return 0;
    },
  },
};
</script>

<style lang="sass" scoped>
.alert
  position: fixed
  background-color: #0069D9
  z-index: 3
  top: 30px
  left: 50%
  transform: scale(1) translateX(-50%)
  border-radius: 20px
  animation: 6s fadeIn both linear
  padding: 0
  border: 0

@keyframes fadeIn
  0%
    opacity: 0

  8%
    opacity: 1

  25%
    opacity: 1

  50%
    opacity: 1

  75%
    opacity: 1

  100%
    opacity: 0

p
  color: white
  font-family: Verdana, Geneva, Tahoma, sans-serif
  font-size: 21px
  margin-bottom: 0
  padding: 20px
  text-align: center

header
  width: 100%
  color: #ddd
  line-height: 40px
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif
  font-size: 25px
  transition: 0.5s
  border-top-right-radius: 20px
  border-top-left-radius: 20px
  height: 40px
  text-align: center

.info
  background-color: yellow

.error
  background-color: red

.ok
  background-color: green
</style>
