<template>
  <div
      elevation="18"
      align="center"
      class=" col-8 col-md-6 col-lg-4 col-xl-3 container"
  >
    <header class="font-weight-bold" cols="12" sm="6" md="3" align="center">
      Zaloguj się
    </header>
    <Alert :info="infoForAlert" :which="whichAlert" v-if="infoForAlert">{{
        infoForAlert
      }}</Alert>
    <form v-on:submit.prevent="onSubmit">
      <v-card-text class="pa-0">
        <div cols="12" sm="6" md="3">
          <v-text-field
              outlined
              required
              class="my-2"
              v-model="email"
              color="primary"
              type="email"
              placeholder="email"
          ></v-text-field>
        </div>
        <div class="row-sm">
          <v-text-field
              v-model="password1"
              type="password"
              outlined
              required
              placeholder="Hasło"
          ></v-text-field>
        </div>

        <button type="submit" class="btn">Zaloguj się</button>
        <div>
          <button @click="changeToRegister" class="login">
            Zarejestruj się!
          </button>
        </div>
      </v-card-text>
    </form>
  </div>
</template>

<script>
import firebase from "firebase";
import Alert from "./Alert.vue";

export default {
  components: {
    Alert,
  },

  inject: ["changeTabs"],

  data() {
    return {
      email: "",
      password1: "",
      infoForAlert: "",
      whichAlert: "",
    };
  },

  watch: {
    infoForAlert() {
      let that = this;

      setTimeout(function() {
        that.infoForAlert = false;
      }, 10000);
    },
  },

  methods: {
    changeToRegister() {
      this.changeTabs("Register");
    },

    onSubmit() {
      firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password1)
          .then((data) => {
            console.log(data);
            this.changeTabs("MainPage");
            this.infoForAlert = "Logging in complete";
            this.whichAlert = "OK";
          })
          .catch((err) => {
            this.infoForAlert = err;
            this.whichAlert = "Error";
          });
    },
  },
};
</script>

<style lang="sass" scoped>
header
  background-color: #03045E
  margin: 0 0 20px 0
  padding: 10px
  border-top-left-radius: 18px
  color: #CAF0f8
  border-top-right-radius: 18px
  position: relative
  top: -10px
  text-transform: uppercase
  font-size: 20px

.container
  background-color: #48CAE4
  padding: 0
  border-radius: 20px
  transform: translateY(20%)

.btn
  border: solid #03045E 1px
  background-color: #03045E
  color: #48CAE4
  text-transform: uppercase
  font-family: sans-serif
  font-weight: bold
  transition: .4s

  &:hover
    color: #03045E
    background-color: #48CAE4

.login
  color: #485693
  font-size: 18px
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif
  margin-top: 12px
  transition: .4s

  &:hover
    color: #263471
    transform: scale(1.2)
</style>
