<template>
  <div :class="{ 'add-height': whichComponent !== 'MainPage' }" class="allApp">
    <Alert :info="infoForAlert" :which="whichAlert" v-if="infoForAlert">{{
        infoForAlert
      }}</Alert>
    <component :is="whichComponent"></component>
  </div>
</template>

<script>
import Register from "./components/Register.vue";
import Login from "./components/Login.vue";
import firebase from "firebase";
import MainPage from "./components/MainPage.vue";
import Alert from "./components/Alert.vue";

export default {
  components: {
    Register,
    Login,
    MainPage,
    Alert,
  },

  watch: {
    infoForAlert() {
      let that = this;

      setTimeout(function() {
        that.infoForAlert = false;
      }, 10000);
    },
  },

  mounted() {
    this.isUser();
  },

  provide() {
    return { changeTabs: this.changeTabs };
  },

  data() {
    return {
      whichComponent: "Login",
      infoForAlert: "",
      whichAlert: "",
    };
  },

  methods: {
    changeTabs(tab) {
      this.whichComponent = tab;
    },

    isUser() {
      firebase.auth().onAuthStateChanged((user) => {
        if (user) {
          this.whichComponent = "MainPage";
          this.infoForAlert = "Użytkownik zalogowany";
          this.whichAlert = "OK";
        } else {
          this.infoForAlert = "Żaden użytkownik nie jest zalogowany";
          this.whichAlert = "Error";
        }
      });
    },
  },
};
</script>

<style lang="sass" scoped>
.allApp
  background: linear-gradient(to bottom, #03045E, #CAF0F8)

.add-height
  height: 100vh
</style>
