<template>
  <div class="app">
    <div class="nav-top text-center">
      <button
          @click="logOut"
          class="logout col-8 col-md-6 col-lg-4 col-xl-3 text-center"
      >
        Wyloguj
      </button>
      <Alert :info="infoForAlert" :which="whichAlert" v-if="infoForAlert">{{
          infoForAlert
        }}</Alert>
    </div>
    <section class="col-xs-11 col-sm-10 col-lg-8 mt-3  mx-auto pb-2 mb-3 ">
      <div class="charts col-xs-11 col-sm-10 col-lg-8 mx-2 ">
        <Chart
            :title="whichCityClicked"
            v-if="isClicked"
            :info="infoOneCity"
            type="Temperatura"
        ></Chart>
        <Chart
            :title="whichCityClicked"
            v-if="isClicked"
            type="Wilgotność"
            :info="infoOneCity"
        ></Chart>
      </div>
      <div
          class="cities d-sm-none d-md-block col-lg-4 mx-sm-0 px-sm-0 mx-lg-2 px-sm-2 "
      >
        <DialogComponent
            v-if="show"
            @btnCancelCity="showAddCity"
            @btnAddCity="addCity"
        ></DialogComponent>
        <button @click="showAddCity" type="button" class="btn btn-primary">
          <span>Dodaj miasto</span>
        </button>
        <Cities
            @tapCity="onSelectCity"
            v-for="city in cities"
            :temp="city.temp"
            :city="city.city"
            :key="city.id"
            :desc="city.desc"
            :icon="city.icon"
        >
        </Cities>
      </div>
    </section>
  </div>
</template>

<script>
import firebase from "firebase";
import Chart from "./Chart.vue";
import DialogComponent from "./DialogComponent.vue";
import Alert from "./Alert.vue";
import Cities from "./Cities.vue";
import { getWeather } from "../methods/getWeatherMain";
import { getDaysWeather } from "../methods/getDaysWeather";

let flagForTimer = true;

export default {
  inject: ["changeTabs"],

  components: {
    Alert,
    DialogComponent,
    Cities,
    Chart,
  },

  mounted() {
    this.cities = [];
    this.getData();
  },

  watch: {
    infoForAlert() {
      let that = this;

      if (flagForTimer) {
        flagForTimer = false;

        setTimeout(function() {
          that.infoForAlert = false;
          flagForTimer = true;
        }, 10000);
      }
    },
  },

  data() {
    return {
      cities: [],
      isClicked: false,
      infoOneCity: [],
      show: false,
      whichCityClicked: "",
      infoForAlert: "",
      whichAlert: "",
    };
  },

  methods: {
    onSelectCity(city) {
      this.infoForAlert = "Wykres pokaże szczegóły pogody";
      this.whichAlert = "Information";
      this.whichCityClicked = city;
      this.isClicked = true;
      let that = this;

      getDaysWeather(city).then((thiscity) => {
        if (thiscity.list[0].dt !== 0) {
          that.infoOneCity = thiscity.list;
        }
      });
    },

    showAddCity() {
      this.show = !this.show;
    },

    addCity(city) {
      let that = this;
      let flag = true;

      this.cities.forEach((cityy) => {
        if (cityy.city === city) {
          flag = false;
        }
      });

      getWeather(city).then((onecity) => {
        if (onecity.cod === 200 && flag) {
          this.cities = [];

          firebase
              .database()
              .ref("cities/" + firebase.auth().currentUser.uid)
              .push(city);

          this.show = !this.show;
          that.infoForAlert = "Miasto zostało dodane";
          that.whichAlert = "OK";

        } else if (!flag) {
          that.infoForAlert = "Miasto jest już w bazie!";
          that.whichAlert = "Error";

        } else {
          that.infoForAlert = "Nie ma takiego miasta!";
          that.whichAlert = "Error";
        }
      });
    },

    getData() {
      this.cities = [];
      let that = this;

      firebase
          .database()
          .ref("cities/" + firebase.auth().currentUser.uid)
          .on("value", function(snapshot) {
            snapshot.forEach(function(childSnapshot) {
              let temp;
              let desc;
              let icon;

              getWeather(childSnapshot.val()).then((city) => {
                temp = city.main.temp;
                desc = city.weather[0].description;
                icon = city.weather[0].icon;

                that.cities.unshift({
                  city: childSnapshot.val(),
                  temp: temp,
                  icon: icon,
                  desc: desc,
                  id: that.cities.length + 1,
                });
              });
            });
          });
    },

    logOut() {
      this.logOutFirebase();
      this.changeTabs("Login");
    },

    logOutFirebase() {
      firebase
          .auth()
          .signOut()
          .then(() => {
            console.log("logout");
          });
    },
  },
};
</script>

<style scoped lang="sass">
.app
  overflow: hidden

section
  overflow: hidden
  background-color: #FFF
  border-radius: 30px
  position: relative
  display: flex
  padding-bottom: 20px
  justify-content: space-around

  .charts
    flex-basis: 68%
    position: relative
    z-index: 2
    margin: 20px

  .cities
    flex-basis: 27%
    overflow-y: scroll
    overflow-x: hidden
    height: 93vh
    position: relative
    margin-bottom: 10px
    margin-top: 10px
    z-index: 2

::-webkit-scrollbar
  width: 20px

::-webkit-scrollbar-track
  box-shadow: inset 0 0 5px #DDD
  border-radius: 10px

::-webkit-scrollbar-thumb
  background: #0069D9
  border-radius: 10px

::-webkit-scrollbar-thumb:hover
  background: #0069D9

.nav-top
  background: linear-gradient(to top, #03045E, #01023B)
  width: 100%

.logout
  text-transform: uppercase
  border: 1px tomato solid
  padding: 10px
  color: white
  border-radius: 20px
  font-size: 23px
  font-weight: 500
  background-color: tomato
  font-family: Verdana, Geneva, Tahoma, sans-serif
  margin-top: 10px
  margin-bottom: 10px

.btn
  cursor: pointer
  background-color: #0069D9
  width: 100%
  height: 64px
  line-height: 64px
  position: relative
  left: 50%
  transform: translateX(-50%)
  z-index: 0

  span
    color: #FFF
    display: block
    text-transform: uppercase
    font: bold 18px/66px Arial
    text-align: center
    top: 0
    letter-spacing: 3px
    left: 0
    right: 0
    transform-origin: center left
    transition: color .3s ease
    position: absolute
    z-index: 1

  &:before, &:after
    content: ''
    background: #02034C
    height: 50%
    width: 0
    position: absolute
    transition: .3s cubic-bezier(.785, .135, .15, .86)

  &:before
    top: 0
    left: 0
    right: auto

  &:after
    bottom: 0
    right: 0
    left: auto

  &:hover
    &:before
      width: 100%
      right: 0
      left: auto

    &:after
      width: 100%
      left: 0
      right: auto

    span
      font-size: 20px
</style>
